import React, { Component } from 'react';
import './AsharaStudios.css';



class AsharaStudios extends Component {
  render() {
    return (
      <div className="AsharaStudios" style={{background: this.props.unity.backgroundColor}}>
        <div className="AsharaStudios-container">
          <div className="AsharaStudios-logoContainer">
            <img className="AsharaStudios-logo" src={this.props.unity.logo} alt={this.props.unity.name}></img>
            <img className="AsharaStudios-logo2" src={this.props.unity.logo2} alt={this.props.unity.name}></img>
            <p style={{color: this.props.unity.color}} className="AsharaStudios-title">{this.props.unity.title1} <br/> {this.props.unity.title2}</p>
          </div>
          <p style={{color: this.props.unity.color}} className="AsharaStudios-description"> {this.props.unity.description}</p>
        </div>
      </div>
    );
  }
}

export default AsharaStudios;