import React, { Component } from 'react';
import { Link } from '@reach/router' 
import Unity from "./unity/Unity";
import AsharaStudios from "./asharastudios/AsharaStudios";
import RedesSociales from "./redesSociales/RedesSociales";
import homeData from 'data/home.json';
import './Home.css';

class Home extends Component {

  render() {
    return (
      <div className="Home">
          { 
            homeData.data.map(
              (unity, index) => {
                if(index === 0){
                  return <AsharaStudios key={index} unity={unity}/>
                }
                else if(index === 7){
                  return <Link key={index} to={unity.link}><Unity unity={unity} width="67%" style={{ textDecoration: 'none', color: 'white' }}/></Link>
                }
                else if(index !== 0){
                  return <Link key={index} to={unity.link}><Unity unity={unity} width="100%" style={{ textDecoration: 'none', color: 'white' }}/> </Link>
                }
                return ""
              }
            )
          }
          <RedesSociales homeData={homeData.data[0]}/>
      </div>
    );
  }
}

export default Home;